import React, { useState } from "react";
import { questions } from "./questions";

export default function App() {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [score, setScore] = useState(0);

  const handleAnswerOptionClick = (isCorrect) => {
    if (isCorrect) {
      setScore(score + 1);
    }

    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < questions.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setShowScore(true);
    }
  };

  const message = () => {
    console.log(`Score es: ${score} y setShow es: ${showScore}`);
    if (score !== questions.length) {
      return <div className="win-message"></div>;
    } else {
      return (
        <div className="win-message">
          ¡Te has hecho acreedor/a del solucionario de la práctica miscelanea de
          esta semana!
          <br></br>
          Solo tienes que enviarme una captura de pantalla a mi número de
          whatsapp {`->`} 959140039 y te la enviaré :D
        </div>
      );
    }
  };
  return (
		<div>

    <div className="app">
      {showScore ? (
				<div>
          <div className="score-section">
            <div>
              Tu puntaje es {score} de {questions.length}
            </div>
            {message()}
            <div>
              <button className="return-option">
                <a href="/">Regresar</a>
              </button>
            </div>
          </div>
        </div>
      ) : (
				<>
          <div className="question-section">
            <div className="question-alert">
            </div>
            <div className="question-count">
              <span>Pregunta {currentQuestion + 1}</span>/{questions.length}
            </div>
            <div className="question-text">
              {questions[currentQuestion].questionText}
            </div>
          </div>
          <div className="answer-section">
            {questions[currentQuestion].answerOptions.map((answerOption) => (
							<button
							onClick={() => handleAnswerOptionClick(answerOption.isCorrect)}
              >
                {answerOption.answerText}
              </button>
            ))}
          </div>
					
        </>
      )}
    </div>
		<p>Desarrollado por Juan Carlos Postigo.</p>
		</div>
  );
}
